import Vue from 'vue'
import VueRouter from 'vue-router'
import DefaultLayout from '../layout/DefaultLayout'
import EventBoard from '../components/dashboard/EventBoard'
import HomeBoard from '../components/dashboard/HomeBoard'
import EntranceLayout from '../layout/EntranceLayout'
import SignIn from '../components/forms/SignIn'
import SignUp from '../components/forms/SignUp'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'EntranceLayout',
        component: EntranceLayout,
        redirect: '/signIn',
        props: true,
        children: [{
                path: '/signIn',
                name: 'SignIn',
                component: SignIn
            },
            {
                path: '/signUp',
                name: 'SignUp',
                component: SignUp
            },
        ]
    },
    {
        path: '/dashBoard',
        name: 'DefaultLayout',
        component: DefaultLayout,
        props: true,
        redirect: '/dashBoard/home',
        children: [{
                path: '/dashBoard/home',
                name: 'HomeBoard',
                component: HomeBoard,
            },
            {
                path: '/dashBoard/chat',
                name: 'EventBoard',
                component: EventBoard
            },
        ],
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router