export const state = () => ({
    userName: ''
});

export const getters = {
    getUserName: (state) => {
        return state.userName
    }
}

export const mutations = {
    setUserName: (state, payload) => {
        state.userName = payload
    }
}

export const actions = {
    setUserName({ commit }, payload) {
        commit('setUserName', payload)
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}