<template>
  <div class="form_body flex_container flex_direction_column">
    <div class="title_area flex_container flex_direction_column">
      <div class="main_title">VIRTUAL SQUARE</div>
      <div class="sub_title">Welcome back! Please login to your account.</div>
    </div>
    <div class="sign_in_form_area flex_container flex_direction_column">
      <form class="sign_in_form" v-show="signupForm">
        <v-text-field
          v-model="name"
          :error-messages="nameErrors"
          label="Name"
          required
          @input="$v.name.$touch()"
          @blur="$v.name.$touch()"
        ></v-text-field>
        <v-text-field
          v-model="email"
          :error-messages="emailErrors"
          label="E-mail"
          required
          @input="$v.email.$touch()"
          @blur="$v.email.$touch()"
        ></v-text-field>
        <v-text-field
          type="password"
          v-model="password"
          :error-messages="passwordErrors"
          label="Password"
          required
          @input="$v.password.$touch()"
          @blur="$v.password.$touch()"
        ></v-text-field>
        <v-text-field
          type="password"
          v-model="confirmPassword"
          :error-messages="confirmPasswordErrors"
          label="Confirm Password"
          required
          @input="$v.confirmPassword.$touch()"
          @blur="$v.confirmPassword.$touch()"
        ></v-text-field>
        <!-- <v-checkbox v-model="checkbox" :error-messages="checkboxErrors" label="Do you agree?" required @change="$v.checkbox.$touch()" @blur="$v.checkbox.$touch()"></v-checkbox> -->
        <div class="btn_area flex_container flex_direction_row">
          <v-btn class="sign_in_btn" dark @click="signUp">Sign Up</v-btn>
        </div>
      </form>
      <form class="sign_in_form" v-show="!signupForm">
        <!-- <v-text-field v-model="name" :error-messages="nameErrors" label="Name" required @input="$v.name.$touch()" @blur="$v.name.$touch()"></v-text-field> -->
        <v-text-field
          v-model="email"
          :error-messages="emailErrors"
          label="E-mail"
          required
          @input="$v.email.$touch()"
          @blur="$v.email.$touch()"
        ></v-text-field>
        <v-text-field
          type="password"
          v-model="verifyCode"
          :error-messages="verifyCodeErrors"
          label="verifyCode"
          required
          @input="$v.verifyCode.$touch()"
          @blur="$v.verifyCode.$touch()"
        ></v-text-field>
        <!-- <v-checkbox v-model="checkbox" :error-messages="checkboxErrors" label="Do you agree?" required @change="$v.checkbox.$touch()" @blur="$v.checkbox.$touch()"></v-checkbox> -->
        <div class="btn_area flex_container flex_direction_row">
          <v-btn class="sign_in_btn" dark @click="userVerify">Activate</v-btn>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, email } from "vuelidate/lib/validators";
import { Auth } from "aws-amplify";

export default {
  mixins: [validationMixin],
  validations: {
    name: {
      required,
    },
    email: {
      required,
      email,
    },
    password: {
      required,
    },
    confirmPassword: {
      required,
    },
    verifyCode: {
      required,
    },
    // checkbox: {
    //     checked(val) {
    //         return val
    //     },
    // },
  },
  data: () => ({
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
    signupForm: true,
    verifyCode: "",
    // checkbox: false,
  }),
  methods: {
    signUpParams() {
      return {
        username: this.email,
        password: this.password,
        attributes: {
          name: this.name,
        },
      };
    },
    signUp() {
      this.$v.$touch();

      const self = this;
      Auth.signUp(self.signUpParams())
        .then((user) => {
          console.log(user);
          self.signupForm = false;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    userVerify() {
      console.log("verify!");
      const self = this;
      Auth.confirmSignUp(self.email, self.verifyCode)
        .then((data) => {
          console.log(data);
          alert("ç»é²å®äºãã¾ãã");
          return self.$router.push("/signIn");
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
  computed: {
    // checkboxErrors() {
    //     const errors = []
    //     if (!this.$v.checkbox.$dirty) return errors
    //     !this.$v.checkbox.checked && errors.push('You must agree to continue!')
    //     return errors
    // },
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.required && errors.push("Name is required.");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email && errors.push("Must be valid e-mail");
      !this.$v.email.required && errors.push("E-mail is required");
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.required && errors.push("Password is required");
      return errors;
    },
    confirmPasswordErrors() {
      const errors = [];
      if (!this.$v.confirmPassword.$dirty) return errors;
      !this.$v.confirmPassword.required && errors.push("Password is required");
      return errors;
    },
    verifyCodeErrors() {
      const errors = [];
      if (!this.$v.verifyCode.$dirty) return errors;
      !this.$v.verifyCode.required && errors.push("verifyCode is required");
      return errors;
    },
  },
};
</script>

